:root{
  --negro: #000;
  --blanco: #fff;
  --plomo: #bcbcb7;
  --plomoOscuro: rgb(151, 151, 151);
}
*{
  padding: 0;
  margin: 0;
}
body{
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}
a{
  text-decoration: none;
}
#root {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 100vh;
}
.todo{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--plomo);
}
.container{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--plomo);
  height: 100vh;
}
.container__h2{
  font-size: 40px;
  margin: 20px;
}
.section{
  width: 85%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--negro);
  height: 70%;
}
.section__contenido{
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60%;
  margin: 15px;
}
.section__img{
  width: 100%;
}

.btnButton{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100px;
  text-align: center;
  padding: 5px;
  margin: 10px;
  background: var(--negro);
  color: var(--blanco);
  border-radius: 5px;
  border: solid var(--blanco);
  font-size: 14px;
  font-family: sans-serif;
}